<template>
  <div class="diy-slider">
    <div v-if="mobile">
      <el-drawer

          :visible.sync="newMobile"
          direction="ltr">
        <SidebarItem ></SidebarItem>
      </el-drawer>
    </div>
    <div v-else>
      <SidebarItem ></SidebarItem>
    </div>
  </div>
</template>

<script>
import SidebarItem from "./SidebarItem";
import {mapState} from "vuex";
export default {
  name: "Index",
  components: {SidebarItem},
  data() {
    return {
    }
  },
  computed:{
    ...mapState("tab",['mobile','closeMobile']),
    newMobile:{
      get(){
        console.log('---')
        return this.closeMobile
      },
      set(val){
        this.$store.commit('tab/setCloseMobile',val)
      }
    }
  }

}
</script>
<style scoped>
.diy-slider{
  width: 100%;
  height: 100%;
}
</style>


