<template>
  <div style="width:100%;z-index: 999">
    <Logo :collapse="!mobile" v-show="!mobile"></Logo>
    <el-menu
        :collapse="isCollapse"
        :default-active="$route.path.split('/')[2]"
        class="el-menu-vertical-demo">
      <el-menu-item :index="item.path" v-for="(item,index) in noChildren" :key="index" @click="clickMenu(item)">
        <i :class="'el-icon-' + item.icon"></i>
        <span slot="title">{{ item.label }}</span>
      </el-menu-item>
      <el-submenu :index="item.path" v-for="(item) in hasChildren" :key="item.path">
        <template slot="title">
          <i :class="'el-icon-' + item.icon"></i>
          <span slot="title">{{ item.label }}</span>
        </template>
        <el-menu-item-group>
          <el-menu-item :index="subItem.path"
                        v-for="(subItem, subIndex) in item.children"
                        :key="subIndex"
                        @click="clickMenu(subItem)" v-show="subItem.name!=='update'">
            <i :class="'el-icon-' + subItem.icon"></i>
            <span slot="title">{{ subItem.label }}</span>
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
import admin from '@/router/admin'
import Logo from "./Logo";
import {mapState} from "vuex";
export default {
  name: "SidebarItem",
  data() {
    return {}
  },
  components: {Logo},
  computed: {
    ...mapState('tab', ['mobile']),
    noChildren() {
      return this.menu.filter(item => !item.children)
    },
    hasChildren() {
      return this.menu.filter(item => item.children)
    },
    isCollapse() {
      return this.$store.state.tab.isCollapse
    },
    menu() {
      return admin
    }
  },
  mounted() {
    // console.log(this.$route)
  },
  methods: {
    clickMenu(item) {
      if (item.name === this.$route.name) {
        return
      }
      this.$router.push({name: item.name})
      this.$store.commit('tab/selectMenu', item)
    }
  }
}
</script>

<style scoped lang="scss">

.el-menu {
  height: 100%!important;
  border: none;

  h3 {
    color: #ffffff;
    text-align: center;
    line-height: 48px;
  }

}

.el-menu-vertical-demo {
  min-height: 100vh;
}

</style>
